.stars {
  display: flex;
  flex-direction: row-reverse;
  justify-content: start;

  &__star {
    background: url("../images/star.svg") no-repeat center;
    width: 16px;
    height: 16px;
    margin-right: 4px;
  }
}

.stars--1 .stars__star:nth-child(5n),
.stars--2 .stars__star:nth-child(1n + 4),
.stars--3 .stars__star:nth-child(1n + 3),
.stars--4 .stars__star:nth-child(1n + 2),
.stars--5 .stars__star:nth-child(1n) {
  background: url("../images/star-active.svg") no-repeat center;
}
